.counter {
  grid-area: counter;
  text-align: center;
  background-color: #00AA8D;
  color: white; }
  .counter .plus, .counter .minus {
    display: inline-block;
    font-size: 20px;
    cursor: pointer;
    margin: 8px;
    width: 30px;
    border-radius: 2px;
    -webkit-box-shadow: 0 10px 14px 0 rgba(0, 0, 0, 0.2);
            box-shadow: 0 10px 14px 0 rgba(0, 0, 0, 0.2);
    -webkit-transition: -webkit-transform 500ms, -webkit-box-shadow 500ms;
    transition: -webkit-transform 500ms, -webkit-box-shadow 500ms;
    -o-transition: transform 500ms, box-shadow 500ms;
    transition: transform 500ms, box-shadow 500ms;
    transition: transform 500ms, box-shadow 500ms, -webkit-transform 500ms, -webkit-box-shadow 500ms;
    color: rgba(255, 255, 255, 0.8);
    background-color: #2ac79b; }
  .counter .plus:hover, .counter .minus:hover {
    -webkit-transform: scale(1.15);
        -ms-transform: scale(1.15);
            transform: scale(1.15);
    -webkit-box-shadow: 0 10px 14px 4px rgba(0, 0, 0, 0.2);
            box-shadow: 0 10px 14px 4px rgba(0, 0, 0, 0.2); }
  .counter .number {
    display: inline;
    font-size: 25px; }



